import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/Home.vue')
  },
  { // 级别
    path: '/login',
    name: 'login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/level',
    name: 'level',
    component: () => import('../views/Level.vue')
  },
  {
    path: '/price',
    name: 'price',
    component: () => import('../views/Price.vue')
  },
  {
    path: '/car',
    name: 'car',
    component: () => import('../views/Car.vue')
  },
  {
    path: '/material',
    name: 'material',
    component: () => import('../views/Material.vue')
  },
  {
    path: '/age',
    name: 'age',
    component: () => import('../views/Age.vue')
  },
  {
    path: '/height',
    name: 'height',
    component: () => import('../views/Height.vue')
  },
  {
    path: '/weight',
    name: 'weight',
    component: () => import('../views/Weight.vue')
  },
  {
    path: '/shape',
    name: 'shape',
    component: () => import('../views/Shape.vue')
  },
  {
    path: '/color',
    name: 'color',
    component: () => import('../views/Color.vue')
  }
  
  
  
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  // let STATUS = true;
  let STATUS = null;

  console.log( localStorage.getItem('userInfo__xes') )
  
  if(localStorage.getItem('userInfo__xes')) {
    STATUS = true
  }


  if(STATUS) {
    if(to.name == 'home') {
      next();
    } else {
      next({name: 'home'});
    }
  } else {
    if(to.name == 'login') {
      next();
    } else {
      next('/login');
    }
  }
})


export default router
