import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import '@/assets/css/reset.css'

Vue.config.productionTip = false



import {Toast, ActionSheet, Field, Cell, CellGroup, Uploader, Button, Picker,Tag,Icon,NavBar,IndexBar,IndexAnchor,TreeSelect,Image,Overlay,Collapse, CollapseItem} from 'vant';

Vue.use(Cell).use(CellGroup).use(Field).use(ActionSheet).use(Toast).use(Uploader).use(Button).use(Picker).use(Tag).use(Icon).use(NavBar).use(IndexBar).use(IndexAnchor).use(TreeSelect).use(Image).use(Overlay).use(Collapse).use(CollapseItem)















new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
